* {
  box-sizing: border-box;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  list-style: none;
}

body {
  margin: 0;
  height: 100vh;
  width: 100wh;
}

.wrapper {
  display: flex;
  flex-direction: row;
}

.container_left {
  min-width: 380px;
  padding: 30px;
}

.favlist {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}

.list {
  min-height: 100vh;
  min-width: 80%;
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
}

li {
  margin-bottom: 10px;
  margin: 20px;
  width: 225px;
  height: 420px;
  text-align: center;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

h1 {
  margin-left: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
}

h2 {
  margin-left: 20px;
  text-align: center;
  color: mediumvioletred;
}

form {
  margin-left: 20px;
}

.input {
  height: 30px;
  border: 0;
  margin-top: 20px;
  min-width: 230px;
  border-bottom: 1px solid mediumvioletred;
}

.button {
  height: 30px;
  width: 60px;
  color: white;
  background-color: mediumvioletred;
  border: 0;
}

.btn {
  height: 30px;
  margin-left: 20px;
  color: mediumvioletred;
  background-color: #cacaca;
  border: 0;
  width: 293px;
}

.favourite {
  background-color: mediumvioletred;
  color: white;
}

.nofavourite {
  background-color: #cacaca;
}

.hidden {
  display: none;
}
